<template>
  <div>
    <ul class="list-group">
      <li class="list-group-item" v-for="article in articles" v-bind:key="article.id">
        <a href="#" v-on:click="openArticleDetail(article.id,article.name)">{{article.name}}:{{article.time}}</a>
      </li>
    </ul>
    <article-detail ref="article"/>
  </div>
</template>

<script>
import articleDetail from './articleDetail'

export default {
  name: 'articles',
  data: function () {
    return {
      articles: []
    }
  },
  components: {
    articleDetail
  },
  created: function () {
    this.$http.get('/static/staticDB/articles').then(res => {
      this.articles = res.body.row
    })
  },
  methods: {
    openArticleDetail: function (articleId, name) {
      this.$refs.article.articleModal(articleId, name)
    }
  }
}
</script>

<style scoped>

</style>



