<template>
  <div id="articleModal" class="modal" tabindex="-1" role="dialog">
    <!--style="position: absolute;height: calc(100vh - 10vh);top: 10vh;z-index: 999;">-->
    <button type="button" class="close modal-footer" aria-label="Close" data-toggle="modal" data-target="#articleModal">
      <span aria-hidden="true">&times;</span>
    </button>
    <div class="modal-dialog" role="document" style="max-width: 100%;height: calc(100% - 4rem);max-height: 100%;">
      <div class="modal-content pre-scrollable" style="max-height: 100%;height: 100%;">
        <div class="modal-body">
          <waiting v-if="isWaiting"/>
          <p>{{article.body}}</p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import waiting from '../waiting'

export default {
  name: 'article-detail',
  data: function () {
    return {
      article: {},
      isShow: false,
      isWaiting: false
    }
  },
  components: {
    waiting
  },
  methods: {
    articleModal: function (articleId, name) {
      $('#articleModal').modal('toggle')
      this.article.id = articleId
      this.getArticle(articleId, name)
    },
    getArticle: function (id, name) {
      this.isWaiting = true
      this.$http.get('/static/articles/' + name + '-' + id + '.txt').then(res => {
        this.isWaiting = false
        this.article.body = res.body
        this.$set(this.article, this.article)
      })
    }
  }
}
</script>

<style scoped>

</style>



