<template>
  <div class="h-100">
    <navigation class="h-25" @switchPage="switchPage"/>
    <component v-bind:is="currentPage"/>
  </div>

</template>

<script>
import Navigation from './navigation'
import portal from './portal'
import QRCode from './QRCode'
import Articles from './articles/articles'
import simpleWord from './simpleWord/simpleWord'

export default {
  data: function () {
    return {
      currentPage: 'portal'
    }
  },
  components: {
    Navigation,
    portal,
    QRCode,
    Articles,
    simpleWord
  },
  name: 'home-page',
  methods: {
    switchPage: function (pageName) {
      this.currentPage = pageName
    }
  }
}
</script>

<style scoped>

</style>



