<template>
  <div class="container">
    <div class="mx-auto">
      <div class="shadow-sm qrCode mx-auto" style="width: 40rem;height: 25rem;">
        <div id="QRContainer" class="card card-body mx-auto" style="width: 12rem;height: 12rem"></div>
        <div class="row">
          <div class="input-group mb-3 mx-auto" style="width: 12rem;">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon1">颜色</span>
            </div>
            <input type="text" class="form-control" placeholder="例:#EEEEEE" aria-describedby="basic-addon1">
          </div>
          <div class="input-group mb-3 mx-auto" style="width: 12rem;">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon2">背景色</span>
            </div>
            <input id="inputBackgroundColor" type="text" class="form-control" placeholder="例:#EEEEEE"
                   aria-describedby="basic-addon2">
          </div>
        </div>
        <textarea id="inputText" class="form-control mx-auto" aria-label="With textarea"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
import qrcodejs from '../../static/libs/jquery-qrcode-0.14.0.min'

export default {
  name: 'QRCode',
  comments: {
    qrcodejs
  },
  created: function () {
    $('input,textarea').on('input change', this.update)
  },
  methods: {
    updateQrCode: function () {
      var options = {
        // render: jq('#render').val(),
        // ecLevel: jq('#eclevel').val(),
        // minVersion: parseInt(jq('#minversion').val(), 10),
        //
        // fill: jq('#fill').val(),
        background: $('#inputBackgroundColor').val(),
        // fill: jq('#img-buffer')[0],

        text: $('#inputText').val(),
        // size: parseInt(jq('#size').val(), 10),
        // radius: parseInt(jq('#radius').val(), 10) * 0.01,
        // quiet: parseInt(jq('#quiet').val(), 10),
        //
        // mode: parseInt(jq('#mode').val(), 10),

        // mSize: parseInt(jq('#msize').val(), 10) * 0.01,
        // mPosX: parseInt(jq('#mposx').val(), 10) * 0.01,
        // mPosY: parseInt(jq('#mposy').val(), 10) * 0.01,

        // label: jq('#label').val(),
        // fontname: jq('#font').val(),
        // fontcolor: jq('#fontcolor').val(),

        image: $('#img')[0]
      }
      $('#QRContainer').empty().qrcode(options)
    },
    update: function () {
      this.updateQrCode()
    }
  }
}
</script>

<style scoped>
  .qrCode {
    width: 10rem;
    height: 20rem;
  }
</style>



