import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/components/homePage'
import QRCode from '@/components/QRCode'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      component: homePage
    },
    {
      path: '/QRCode',
      name: 'QRCode',
      component: QRCode
    }

  ]
})



