<template>
  <div class="float-right shadow-sm">
    <waiting v-if="waitingOpen"/>
    <notice-modal ref="noticeModal"/>
    <ul class="pre-scrollable list-group" style="max-height: 85vh;">
      <li v-for="(notice,index) in notices" v-bind:key="notice.time+index" class="list-group-item">
        <div class="" style="width: 20rem;max-height: 10rem;min-height: 5rem">
          <span class="text-primary text-capitalize" v-on:click="openDetail(notice)" style="cursor: pointer">{{notice.title}}</span>
          <span class="text-muted">{{notice.time}}</span>
          <p class="text-body text-muted text-cut">{{notice.body}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import 'vue-resource'
import Waiting from '../waiting'
import noticeModal from './noticeModal'

export default {
  components: {Waiting, noticeModal},
  name: 'notice',
  weight: 'weight:5rem',
  data: function () {
    return {
      notices: [],
      waitingOpen: false
    }
  },
  created: function () {
    this.waitingOpen = true
    this.$http.get('/static/staticDB/notices').then(res => {
      this.notices = res.body
      this.waitingOpen = false
    })
  },
  methods: {
    openDetail: function (notice) {
      this.$refs.noticeModal.noticeModal(notice)
    }
  }
}
</script>

<style scoped>
  .text-cut {
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>



