<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light" role="navigation">
    <waiting v-show="waitingOpen"/>
    <a class="navbar-brand" href="#" v-on:click="switchPage('portal')">{{project.projectName}}</a>
    <img src="../../static/img/siteQRCode.png" class="siteQRCode"/>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav" v-for="tool in headerToolList" v-bind:key="tool.name">
        <li class="nav-item" v-if="tool.url!==''&&tool.upperLayer===''">
          <a class="nav-link" href="#" v-on:click="switchPage(tool.url)">
            {{tool.name}}
          </a>
        </li>
        <li class=" nav-item dropdown" v-if="tool.url===''">
          <a href="#" id="navbarDropdown" class="nav-link dropdown-toggle" data-toggle="dropdown" role="button"
             aria-haspopup="true" aria-expanded="false">
            {{tool.name}}
          </a>
          <div class="dropdown-menu" aria-labelledby="navbarDropdown">
            <a class="dropdown-item" href="#" v-on:click="switchPage(subTool.url)" v-for="subTool in headerToolList"
               v-if="subTool.upperLayer===tool.id"  v-bind:key="subTool.name">{{subTool.name}}</a>
          </div>
        </li>
      </ul>
    </div>
    <div class="input-group input-group-sm mb-3" style="width: 12rem;margin-right: 2rem;">
      <input type="text" class="form-control" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary" type="button">搜索</button>
      </div>
    </div>
  </nav>
</template>

<script>
import 'vue-resource'
import Waiting from './waiting'

export default {
  components: {
    Waiting
  },
  name: 'navigation',
  data: function () {
    return {
      project: {},
      headerToolList: [],
      waitingOpen: false
    }
  },
  created: function () {
    this.waitingOpen = true
    this.$http.get('/static/staticDB/project').then(res => {
      if (res.body.row[0] !== undefined) {
        this.project = res.body.row[0]
      }
      this.waitingOpen = false
    })
    this.$http.get('/static/staticDB/headerToolList').then(res => {
      this.headerToolList = JSON.parse(res.bodyText).row
      this.waitingOpen = false
    })
  },
  methods: {
    switchPage: function (pageName) {
      this.$emit('switchPage', pageName)
    }
  }
}
</script>

<style scoped>
  .siteQRCode {
    height: 2rem;
  }
</style>



