<template>
  <div id="noticeModal" class="modal fade" tabindex="-1" role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">{{notice.title}}</h5>
          <span class="text-muted align-self-end">{{notice.time}}</span>
          <button type="button" class="close" data-toggle="modal" data-target="#noticeModal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <p class="text-body">{{notice.body}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'modal',
  data: function () {
    return {
      modalOpen: false,
      notice: {}
    }
  },
  methods: {
    noticeModal: function (notice) {
      this.notice = notice
      $('#noticeModal').modal('toggle')
    }
  }
}
</script>

<style scoped>
</style>



